import { Button } from "@/components/ui/button";
import {
  Sheet,
  SheetClose,
  SheetContent,
  SheetDescription,
  SheetFooter,
  SheetHeader,
  SheetTitle,
  SheetTrigger,
} from "@/components/ui/sheet";
import { useCartStore } from "../store/cartStore";

export function Cart() {
  const items = useCartStore(state => state.items);
  return (
    <Sheet>
      <SheetTrigger asChild>
        <button className="bg-[#90D116] text-white px-4 py-2 rounded-md hover:bg-[#72A612] transition mr-6">
          Cotizador
        </button>
      </SheetTrigger>
      <SheetContent>
        <SheetHeader>
          <SheetTitle>Cotización</SheetTitle>
          <SheetDescription>
            Se muestra la lista de los elementos que desea
            cotizar
          </SheetDescription>
        </SheetHeader>
        <div className="grid flex-1 auto-rows-min gap-6 px-4">
          <ul>
            {items.map(item => (
              <li key={item.id}>
                Product ID: {item.id} - Quantity:{" "}
                {item.quantity}
              </li>
            ))}
          </ul>
        </div>
        <SheetFooter>
          <Button
            className="bg-emerald-400 hover:bg-emerald-600 cursor-pointer"
            type="submit">
            Cotizar
          </Button>
          <SheetClose asChild>
            <Button
              className="cursor-pointer"
              variant="outline">
              Cerrar
            </Button>
          </SheetClose>
        </SheetFooter>
      </SheetContent>
    </Sheet>
  );
}
